@import '../variables';

.categories {
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }

  @media (max-width: 1280px) {
    & {
      padding: 10px 0;
      overflow: scroll;

      ul {
        flex-wrap: nowrap;
      }
    }
  }

  @media (max-width: 1024px) {
    & {
      ul {
        li {
          padding: 10px 20px;
        }
      }
    }
  }

  @media (max-width: 768px) {
    & {
      padding: 5px 0;
      margin-bottom: 15px;

      ul {
        font-size: 12px;

        li {
          padding: 10px 15px;
        }
      }
    }
  }

  @media (max-width: 425px) {
    & {
      max-width: 100%;
      padding: 10px 0;
      margin-bottom: 10px;

      ul {
        font-size: 12px;

        li {
          padding: 10px 15px;
        }
      }
    }
  }
}
